/* ----------------------------------------------------------------------------
About: A hexmap showing the results of the 2015 local elections for councils
electing in the 2019 local elections.
Author: Oliver Hawkins
*/

/* Imports ----------------------------------------------------------------- */

import "whatwg-fetch";
import * as d3 from "d3";
import * as d3_hexjson from "d3-hexjson";
import * as pym from "pym.js";

/* Constants --------------------------------------------------------------- */

const DEFAULT_WIDTH = 750;

/* Imports ----------------------------------------------------------------- */

function draw(hexjson, outerWidth) {

    // State
    let activeGroup = null;
    let activeHex = null;

    // Sizes and margins
    const margin = {top: 10, right: 10, bottom: 10, left: 10},
        width = outerWidth - margin.left - margin.right,
        height = Math.floor(outerWidth * 0.85) - margin.top - margin.bottom,
        viewBox = "0 0 " + width + " " + height;

    // Render hexes
    const hexes = d3_hexjson.renderHexJSON(hexjson, width, height);
    const datahexes = d3_hexjson.renderHexJSON(hexjson, width, height);

    // Create the svg element
    const svg = d3
        .select("#vis")
        .attr("viewBox", viewBox)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // Select the infobox
    const infobox = d3.select("#infobox");

    // Add background canvas for click events
    svg
        .append("rect")
        .attr("width", "100%")
        .attr("height", "100%")
        .attr("stroke", "#ffffff")
        .attr("stroke-width", 0)
        .attr("stroke-opacity", 0)
        .attr("fill", "#ffffff")
        .attr("fill-opacity", 0)
        .on("click", () => {
            if (activeGroup !== null && activeHex !== null) {
                activeGroup.attr("stroke-opacity", getOpacity(activeHex.p));
                activeGroup.attr("fill-opacity", getOpacity(activeHex.p));
                activeGroup.attr("fill", getColor(activeHex.p));
                hideInfoBox(infobox);
                activeGroup = null;
                activeHex = null;
            }
        });

    // Create and render the hexgrid
    const hexgrid = svg
        .selectAll("g.grid")
        .data(hexes)
        .enter()
        .append("g")
        .attr("transform", hex => "translate(" + hex.x + "," + hex.y + ")");

    hexgrid
        .append("polygon")
        .attr("points", hex => hex.points)
        .attr("stroke", "#b0b0b0")
        .attr("stroke-width", 1)
        .attr("fill", "#f0f0f0");

    // Create and render the hexmap
    const hexmap = svg
        .selectAll("g.data")
        .data(datahexes)
        .enter()
        .append("g")
        .attr("transform", hex => "translate(" + hex.x + "," + hex.y + ")");

    hexmap
        .append("polygon")
        .attr("points", hex => hex.points)
        .attr("stroke", "#ffffff")
        .attr("stroke-width", 2)
        .attr("stroke-opacity", hex => getOpacity(hex.p))
        .attr("fill-opacity", hex => getOpacity(hex.p))
        .attr("fill", hex => getColor(hex.p))
        .on("mouseover", (hex, i , n) => {
            const g = d3.select(n[i]);
            g.attr("stroke-opacity", 1);
            g.attr("fill-opacity", 1);
            g.attr("fill", "#80ffff");
            showInfoBox(infobox, hex.n, hex.p, hex.q);
            activeGroup = g;
            activeHex = hex;
        })
        .on("mouseout", (hex, i , n) => {
            const g = d3.select(n[i]);
            g.attr("stroke-opacity", getOpacity(hex.p));
            g.attr("fill-opacity", getOpacity(hex.p));
            g.attr("fill", getColor(hex.p));
            hideInfoBox(infobox);
            activeGroup = null;
            activeHex = null;
        });
}

/* Infobox ----------------------------------------------------------------- */

// Shows the infobox with the given data
function showInfoBox(infobox, name, party, q) {

    const html = `
        <p class="infobox-label">
            ${name}
        </p>
        <p class="infobox-label" style="font-weight: bold; color: ${getColor(party)};">
            ${party}
        </p>`;

    infobox.html(html);

    const box = infobox.node().getBoundingClientRect();
    const boxHeight = box.height;
    const boxWidth = box.width;

    let infoBoxPosX = d3.event.pageX,
        infoBoxPosY = d3.event.pageY - boxHeight;

    if (q > 13) infoBoxPosX = d3.event.pageX - boxWidth;

    infobox
        .style("left", infoBoxPosX + "px")
        .style("top", infoBoxPosY + "px");

    infobox
        .transition()
        .duration(200)
        .style("opacity", .96);
}

// Hides the infobox
function hideInfoBox(infobox) {

    infobox
        .transition()
        .duration(500)
        .style("opacity", 0);
}

// Fill function
function getColor(party) {

    switch (party) {
    case "Conservative":
        return "#00539f";
    case "Labour":
        return "#d50000";
    case "Liberal Democrat":
        return "#faa01a";
    case "UK Independence Party":
        return "#722889";
    case "No overall control":
        return "#909090";
    case "Residents&apos; Associations <br/> of Epsom and Ewell":
        return "#404040";
    case "New council":
        return "#ffaaff";
    default:
        return "#909090";
    }
}

// Opacity function
function getOpacity(party) {
    return (party == "No elections") ? 0 : 1;
}

/* Global execution -------------------------------------------------------- */

d3.json("election-results-2015.json"). then((hexjson) => {
    draw(hexjson, DEFAULT_WIDTH);
    new pym.Child();
});
